/* eslint-disable */
import React, { useContext, useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import styled from "styled-components";
import './Detail.scss'
import { Nav } from 'react-bootstrap';
import { Rangecontext } from './App.js'
import { CSSTransition } from 'react-transition-group';

let Box = styled.div`
  padding : 20px;
`;

let Title = styled.h4`
  font-size : 25px;
  color : ${props => props.Color}
`;



function Detail(props) {

  let [alert, alertChange] = useState(true);
  let [pushTab, pushTabChange] = useState(0)
  let Range = useContext(Rangecontext);

  useEffect(() => {
    let Timer = setTimeout(() => { alertChange(false) }, 2000);
    return () => { clearTimeout(Timer); }
  }, [alert]);


  let { id } = useParams();
  let history = useHistory()
  let product = props.shoes.find(function (e) {
    return e.id == id
  });





  return (
    <div className="container">
      <Box>
        <Title className='red'>상세페이지</Title>
      </Box>

      {
        alert == true
          ? (<div className="my-alert2">
            <p> 재고가 얼마 남지 않았습니다</p>
          </div>)
          : null
      }



      <div className="row">
        <div className="col-md-6">
          <img src={"https://codingapple1.github.io/shop/shoes" + (parseInt(id) + 1) + ".jpg"} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{product.title}</h4>
          <p>{product.content}</p>
          <p>{product.price}</p>

          <ProductInfo product={props.product}> </ProductInfo>

          <button className="btn btn-danger" onClick={() => { props.productChange([9, 10, 11]) }}>주문하기</button>&nbsp;
          <button className="btn btn-danger" onClick={() => {
            // history.push('/'); 어느특정 경로로 이동하고싶을떄
            // history.goForward(); 앞으로 이동..
            history.goBack();
          }}>뒤로가기</button>
        </div>
      </div>
      <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={() => { pushTabChange(0) }}>Option 0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={() => { pushTabChange(1) }}>Option 1</Nav.Link>
        </Nav.Item>
      </Nav>

      <TabController pushTab={pushTab} />

    </div>
  )
}

function TabController(props) {
  if (props.pushTab === 0) {
    return <div>0번째 내용입니다.</div>
  }
  else if (props.pushTab === 1) {
    return <div>1번째 내용입니다.</div>
  }
  else {
    return <div>2번째 내용입니다.</div>
  }

}

function ProductInfo(props) {
  return (
    <p>재고 : {props.product[0]}</p>
  );
}

export default Detail;